.input {
	display: flex;
	width: 90%;
	position: relative;
	align-items: center;
}

.input__box {
	width: 100%;
	border-radius: 50px;
	padding: 20px 30px;
	font-size: 25px;
	border: none;
	transition: .2s;
	box-shadow: inset 0 0 5px black;
}

.input__box:focus {
	box-shadow: 0 0 10px 1000px rgba(34, 31, 49, 0.4);
	outline: none;
}

.input__submit {
	position: absolute;
	width: 50px;
	height: 50px;
	margin: 12px;
	border-radius: 50px;
	right: 0;
	border: none;
	font-size: 15px;
	background-color: rgba(0, 125, 129, 0.787);
	color: white;
	transition: .2s all;
	box-shadow: 0 0 10px black;
}

.input__submit:hover{
	background-color: rgba(2, 178, 184, 0.698);
}

.input__submit:active{
	transform: scale(0.8);
	box-shadow: 0 0 5px black;
}

.todos {
	display: flex;
	justify-content: space-evenly;
	width: 90%;
	flex-wrap: wrap;
}

.todo__component {
	display: flex;
	width: 29%;
	border-radius: 5px;
	padding: 20px;
	margin-top: 15px;
	background-color: rgba(253, 236, 1, 0.897);
}

.todo__component--text {
	flex: 1;
	border: none;
	padding: 5px;
	font-size: 20px;
}

.todo__component--text:focus {
	outline: none;
}

.icon {
	margin-left: 10px;
	font-size: 25px;
	cursor: pointer;
}

@media (max-width: 700px) {
	.input {
		width: 95%;
	}

	.todos {
		width: 95%;
	}

	.todo__component {
		width: 95%;
	}


}